@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 400;
  src: url(../../fonts/gilroy-regular.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  src: url(../../fonts/gilroy-bold.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  src: url(../../fonts/gilroy-medium.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 600;
  src: url(../../fonts/gilroy-semibold.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 800;
  src: url(../../fonts/Gilroy-ExtraBold.otf);
}
* {
  font-family: "Gilroy", "Arial", sans-serif;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: content-box;
}

*::before,
*::after {
  box-sizing: inherit;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  max-width: 1200px;
  margin: 0 auto;
}

.header-wrapper {
  display: flex;
  align-items: center;
  padding-top: 24px;
}

.menu__headr-list {
  display: flex;
  padding-left: 0;
}

.menu__header-link {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #2C2F36;
  text-decoration: none;
  padding-right: 72px;
  position: relative;
  content: "";
}

.menu__header-link:hover {
  color: #FB8C00;
}

.menu__header-link:active,
.menu__header-link:focus {
  color: #cf7404;
}

.menu__header-link--active:hover:before {
  position: absolute;
  content: "";
  width: 81px;
  height: 3px;
  background-color: #FB8C00;
  bottom: -5px;
  left: 0;
}

.button__print {
  font-weight: bold;
  font-size: 16px;
  line-height: 140%;
  color: #FFFFFF;
  background: #FB8C00;
  border-radius: 88px;
  padding-left: 58px;
  padding-right: 30px;
  padding-top: 13px;
  padding-bottom: 13px;
  border: none;
  position: relative;
  content: "";
}

.button__print:hover {
  background-color: #2F80ED;
}

.button__print:active,
.button__print:focus {
  background-color: #0649a0;
}

.button__print::before {
  position: absolute;
  content: "";
  background-image: url(../../img/index/print.svg);
  width: 12px;
  height: 13px;
  left: 32px;
  bottom: 17px;
}

.header__logo {
  padding-right: 314px;
}

.about-wrapper {
  width: 100%;
  padding-bottom: 300px;
}

.about {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 200px;
  display: flex;
}

.about-title {
  font-weight: 800;
  font-size: 72px;
  line-height: 88px;
  color: #2C2F36;
  max-width: 370px;
  padding-top: 109px;
  margin-bottom: 199px;
  position: relative;
  content: "";
}

.about-title::after {
  position: absolute;
  content: "";
  width: 72px;
  height: 3px;
  background-color: #FB8C00;
  bottom: -32px;
  left: 0;
}

span {
  color: #FB8C00;
}

.about-list {
  display: flex;
  margin: 0;
  list-style: none;
}
.about-list__item:first-child {
  padding-left: 0;
  max-width: 162px;
}
.about-list__item:nth-child(2) {
  max-width: 195px;
}
.about-list__item:last-child {
  max-width: 120px;
}

.about-list__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #2C2F36;
}

.about-list__item {
  padding-left: 63px;
}

.about-picture__img {
  padding-top: 80px;
}

.catalog-wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding-top: 73px;
  padding-bottom: 194px;
}

.title {
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  color: #2C2F36;
}

.catalog__title {
  padding-bottom: 148px;
}

.gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 126px 30px;
}

.products {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  background-color: #ebf6ff;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 35px;
  max-width: 370px;
  max-height: 244px;
  padding-bottom: 40px;
}

.products__subtitle {
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #2C2F36;
  text-align: center;
}

.products__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #2C2F36;
  width: 228px;
  height: 44px;
  text-align: center;
  padding-top: 16px;
}

.products:nth-of-type(1) > .products__subtitle {
  max-width: 215px;
}

.products:nth-of-type(7) > .products__subtitle {
  max-width: 280px;
}

.products:nth-of-type(8) > .products__text {
  width: 262px;
}

.products__img {
  display: block;
}

.products:nth-of-type(1) > .products__img {
  transform: translate(0, -33px);
}

.products:nth-of-type(2) > .products__img {
  transform: translate(0, -48px);
}

.products:nth-of-type(3) > .products__img {
  transform: translate(0, -55px);
}

.products:nth-of-type(4) > .products__img {
  transform: translate(0, -32px);
}

.products:nth-of-type(5) > .products__img {
  transform: translate(0, -54px);
}

.products:nth-of-type(6) > .products__img {
  transform: translate(0, -40px);
}

.products:nth-of-type(7) > .products__img {
  transform: translate(0, -45px);
}

.products:nth-of-type(8) > .products__img {
  transform: translate(0, -47px);
}

.products:nth-of-type(9) > .products__img {
  transform: translate(0, -45px);
}

.work-wrapper {
  width: 100%;
  padding-bottom: 350px;
}

.work {
  max-width: 1387px;
}

.work-container {
  display: flex;
  justify-content: space-between;
  padding-top: 64px;
}

.work__title {
  padding-left: 215px;
}

.work-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.work-list__item::before {
  position: absolute;
  content: "";
  background-image: url(../../img/index/Ellipse.svg);
  background-repeat: no-repeat;
  width: 168px;
  height: 168px;
  top: -59px;
  left: -68px;
  overflow: hidden;
}

.work-list__text {
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #2C2F36;
}

.work-list__item:nth-child(1) > .work-list__text {
  max-width: 378px;
  padding-right: 55px;
  padding-left: 137px;
  padding-top: 50px;
  padding-bottom: 50px;
}

.work-list__item:nth-child(5) > .work-list__text {
  max-width: 136px;
  padding-left: 116px;
  padding-top: 38px;
  padding-bottom: 38px;
  padding-right: 19px;
}

.work-list__text {
  max-width: 128px;
  padding-right: 25px;
  padding-left: 118px;
  padding-top: 50px;
  padding-bottom: 50px;
}

.work-list__item:nth-child(3) > .work-list__text {
  padding-right: 25px;
  padding-left: 118px;
  padding-top: 38px;
  padding-bottom: 38px;
}

.work-list__item {
  box-shadow: 0px 4px 68px rgba(32, 52, 91, 0.08);
  border-radius: 35px;
  width: 271px;
  height: 151px;
  position: relative;
  content: "";
  overflow: hidden;
}

.work-list__item:nth-child(1) {
  grid-column: 1/3;
  box-shadow: 0px 4px 68px rgba(32, 52, 91, 0.08);
  border-radius: 35px;
  width: 570px;
  height: 151px;
}

.footer {
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 180px;
}